html {
    font-size: 62.5%;
    box-sizing: border-box;
    color: aqua;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_principal;
    font-size: 1.6rem;
    min-height: 100%;
    @include tablet {
        height: 100%;
    }
}
p {
    color: $blanco;
    font-size: 2rem;
    text-align: center;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

h1, h2, h3, h4 {
    font-family: $fuente_principal;
    margin: 0 0 $separacion / 2 0 ;
    font-weight: 900;
    color: $azul;
    text-align: center;
}

h1 {
    font-size: 7rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}

h4 {
    font-size: 3rem;
}