.titulo-login {
    margin: 2rem 0 4rem 0;
}

.parrafo-login {
    margin-bottom: 4rem;
}

.boton-block {
    background-color: $azul;
    color: $blanco;
    font-weight: $bold;
    margin: 2rem 0;
    padding: 1rem 2rem;
    border-radius: 0.75rem;
    border: none;
    cursor: pointer;
}

.boton-block-rojo {
    background-color: $rojo;
    color: $blanco;
    font-weight: $bold;
    margin: 2rem 0;
    padding: 1rem 1.5rem;
    border-radius: 0.75rem;
    cursor: pointer;
    border: none;
}

.boton-block-amarillo {
    background-color: $amarillo;
    color: $blanco;
    font-weight: $bold;
    margin: 2rem 0;
    padding: 1rem 1.5rem;
    border-radius: 0.75rem;
    cursor: pointer;
    border: none;
}

.boton-block-derecha {
    background-color: $azul;
    color: $blanco;
    font-weight: $bold;
    margin: 2rem 0;
    padding: 1rem 1.5rem;
    border-radius: 0.75rem;
    float: right;
    cursor: pointer;
    border: none;
}

.loginExtra {
    @include tablet {
        display: flex;
        justify-content: space-between;
    }
    p, a {
        color: $blanco;
        font-size: 1.5rem;
    }
    .password {
        align-self: center; 
        text-align-last: center;
    }
}

.registro-extra {
    display: flex;
    justify-content: space-between;
    a {
        background-color: $azul;
        text-decoration: none;
        font-size: 2rem;
        margin: 2rem 0;
        padding: 1rem 2rem;
        color: $blanco;
        font-weight: $bold;
        border-radius: 0.75rem;
    }
    .volver {
        align-self: center;
    }
}