.mostrar-mensaje {
    text-transform: uppercase;
    color: $blanco;
    text-align: center;
}

.alerta {
    color: $blanco;
    padding: $separacion / 2;
    text-align: center;
    width: 100%;
    display: block;
    &.error {
        background-color: #a50606;
    }
}

.contenido-resumen {
    p {
        margin-bottom: 0.5rem;
    }
    span {
        color: $azul;
        font-weight: $bold;
        text-transform: uppercase;
    }
    .resumen-servicios {
        h3 {
            margin: 2.5rem 0;
        }
        .contenedor-servicio {
            margin-bottom: 2rem;
            padding-bottom: 2rem;
            border-bottom: 1px solid #43453c;
            .precio {
                color: $azul;
                font-weight: $bold;
            }
        }
    }
}

.total {
    text-align: right;
}