.tabs {
    //@include grid(3, 2rem);
    display: grid;
    row-gap: 1rem;
    @include tablet {
        @include grid(3, 2rem);
    }
    button {
        background-color: $blanco;
        font-size: 1.4rem;
        font-weight: $bold;
        padding: 1rem;
        border: none;
        text-align: center;
        text-transform: uppercase;
        border-radius: 1rem;
        &.actual {
            background-color: $azul;
            color: $blanco;
        }
        &:hover {
            cursor: pointer;
        }
    }
}

.busqueda {
    margin-top: 5rem;
}

.busqueda-datos {
    .alinear-derecha {
        text-align: right;
    }
    p {
        text-align: left;
        color: $azul;
        font-weight: $bold;
        span {
            color: $blanco;
            font-weight: $regular;
        }
    }
}

.busqueda-servicios {
    @include tablet() {
        display: flex;
        justify-content: space-between;
    }
}

.busqueda-servicios-admin {
    @include tablet() {
        .servicio {
            align-self: center;
        }
        @include grid(4, 1fr);
    }
}

.boton {
    background-color: $blanco;
    font-size: 1.4rem;
    font-weight: $bold;
    padding: 1rem;
    border: none;
    text-align: center;
    text-transform: uppercase;
    border-radius: 1rem;
    color: $negro;
    &.actual {
        background-color: $azul;
        color: $blanco;
    }
    &:hover {
        cursor: pointer;
    }
}